@import "../../var.css";

.header {
  width: 100vw;
  height: 60px;
  justify-content: space-between;
  align-items: center;
  background-color: var(--primary-color);
  padding: 16px 120px 16px 120px;
  display: flex;
  position: fixed;
  border-bottom: 1px solid hsl(from var(--primary-color) h s 50%);
  z-index: 4;
}
#logo {
  width: 191px;
}
.group {
  align-items: center;
}
.navlinks,
.group {
  list-style: none;
  display: flex;
  gap: 20px;
}
.navlinks button {
  background: none;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 16px;
  position: relative;
  padding: 0 10px;
}

.navlinks a,
.dropbtn {
  color: white;
  text-decoration: none;
  background: none;
  border: none;
  cursor: pointer;
}
.downarrow {
  position: absolute;
  right: -8px;
  bottom: 2px;
}
.dropdown {
  position: relative;
}

.dropdown-content {
  position: absolute;
  top: 41px;
  background: var(--primary-color);
  border: 1px solid hsl(from var(--primary-color) h s 30%);
  list-style: none;
  border-radius: 3px;
  width: 155px;
}

.dropdown-content li a {
  color: var(--text-color);
  display: block;
  padding: 8px 10px;
  border-radius: 3px;
}

.dropdown-content li a:hover {
  background: hsl(from var(--primary-color) h s 30%);
}

.btn-group {
  display: inherit;
  gap: 13px;
  margin-left: 5px;
  position: relative;
}

.button {
  height: 40px;
  width: fit-content;
  text-align: center;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  transition: 300ms ease;
  font-weight: 550;
}
.login-btn {
  padding: 0px 30px;
  background-color: var(--btn1-color);
  color: var(--primary-color);
}
.login-btn:hover {
  background-color: hsl(from var(--btn1-color) h s 68%);
}
.try-btn {
  padding: 0 24px;
  background-color: var(--btn2-color);
  color: var(--text-color);
}
.try-btn:hover {
  background-color: hsl(from var(--btn2-color) h s 60%);
}
.bars,
.close {
  position: absolute;
  font-size: 36px;
  top: 1px;
  right: -38px;
  color: var(--navicon-color);
  display: none;
  cursor: pointer;
}

@media (max-width: 1440px) {
  .header {
    padding: 0 45px;
  }
  .bars,
  .close {
    display: none;
  }
}
@media (max-width: 1152px) {
  .header {
    position: relative;
  }
  .navlinks {
    flex-direction: column;
    gap: 0;
    position: absolute;
    top: 60px;
    right: 0;
    background-color: var(--primary-color);
    width: 200px;
    border-radius: 3px;
    padding: 0 0 8px 0;
    transform: translateY(-150vh);
    transition: 800ms;
  }
  .displaymenu {
    transform: none;
  }
  .navlinks li {
    padding: 15px 0;
    width: 100%;
    text-align: center;
  }
  .downarrow {
    left: -3;
  }
  .bars {
    display: block;
  }
  .dropdown li a {
    padding: 1px 10px;
  }
  .dropdown-content {
    left: -77%;
    padding: 5px 0;
  }
}
@media (max-width: 768px) {
  .login-btn,
  .try-btn {
    display: none;
  }
  .bars,
  .close {
    top: -20px;
  }
}
@media (max-width: 353px) {
  .header {
    padding: 0 20px;
  }
  .navlinks {
    width: 100%;
  }
  #logo {
    width: 134px;
  }
  .bars,
  .close {
    font-size: 32px;
    right: -15px;
  }
  .dropdown {
    position: static;
  }

  .dropdown-content {
    position: static;
    width: 100%;
    background: hsl(from var(--primary-color) h s 20%);
    padding: 5px 0;
  }
}
