.footer {
  background-color: #002e6d;
  color: white;
  padding: 70px 40px 0 40px;
  font-family: Arial, sans-serif;
  width: 100vw;
}

.footer-container {
  max-width: 100vw;
  margin: auto;
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr 2fr;
  gap: 40px;
}

/* Logo */
.footer-brand p {
  margin-top: 15px;
  color: #d2d9e8;
  line-height: 1.6;
}

.logo img {
  height: 34px;
}

.logo-icon {
  background: white;
  color: #002e6d;
  padding: 4px 8px;
  border-radius: 4px;
}

/* Columns */
.footer-column h4 {
  margin-bottom: 15px;
  font-size: 16px;
}

.footer-column a {
  display: block;
  color: #d2d9e8;
  text-decoration: none;
  margin-bottom: 10px;
  font-size: 14px;
}

.footer-column a:hover {
  color: white;
}

/* CTA Box */
.footer-cta h3 {
  font-size: 20px;
  margin-bottom: 10px;
}

.footer-cta p {
  font-size: 14px;
  color: #d2d9e8;
  margin-bottom: 15px;
}

.footer-cta button {
  background-color: #4ea1ff;
  border: none;
  padding: 12px 24px;
  color: white;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
}

.footer-cta button:hover {
  background-color: #348cff;
}

/* Bottom Section */
.footer-bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  margin-top: 50px;
  padding: 25px 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.footer-links {
  display: flex;
  gap: 20px;
  font-size: 14px;
  color: #d2d9e8;
}

.footer-links a {
  color: #d2d9e8;
  text-decoration: none;
}

.footer-links a:hover {
  color: white;
}

/* Social Icons */
.footer-social {
  display: flex;
  gap: 18px;
  font-size: 16px;
  cursor: pointer;
}

.footer-social svg:hover {
  color: #4ea1ff;
}
