@import "../../var.css";
#Work {
  width: 100%;
  padding: 140px 45px;
  background-image: url("../../global/bg.png");
  background-repeat: no-repeat;
  background-position: -50px -200px;
  display: flex;
  flex-direction: column;
  row-gap: 100px;
}
.project-container,
.work-container {
  display: flex;
  align-items: center;
}
.project-image {
  background-image: url(../../global/projectimage.jpg);
}
.work-container {
  column-gap: 160px;
}
.work-image {
  background-image: url("../../global/work.png");
  background-repeat: no-repeat;
  background-size: contain;
  width: 583px;
  aspect-ratio: 1/1;
}
.project-content,
.work-content {
  width: 55%;
  align-content: center;
}
.work-content {
  width: 68%;
}
.project-content h1,
.work-content h1 {
  font-size: 72px;
  position: relative;
  margin-bottom: 24px;
}
.element {
  position: absolute;
  bottom: -15px;
  left: 0;
  z-index: -1;
}
.work-element {
  width: 55%;
  left: 220px;
  bottom: -5px;
}
.work-content p,
.project-content p {
  font-size: 18px;
  margin-bottom: 60px;
}
@media (max-width: 1143px) {
  .project-content h1,
  .project-content p {
    width: 436px;
  }
  .work-element {
    left: 40px;
    bottom: 0;
  }
  .project-image {
    height: 40%;
  }
}
@media (max-width: 1040px) {
  .project-image {
    width: 50%;
    aspect-ratio: 1.5/1;
    margin-left: 10px;
  }
}
@media (max-width: 930px) {
  .project-content h1,
  .work-content h1 {
    font-size: 54px;
  }
  .project-content p {
    width: 90%;
  }
  .project-element {
    width: 80%;
    bottom: -5px;
    left: 20px;
  }
  .work-element {
    width: 100%;
    left: 0;
  }
}
@media (max-width: 768px) {
  #Work,
  .project-container,
  .work-container {
    flex-direction: column;
  }
  .project-image {
    margin-top: 20px;
    width: 500px;
  }
  .work-container {
    flex-direction: column-reverse;
  }
  .work-image {
    width: 400px;
    margin-top: 30px;
  }
}
