@import "../../var.css";
#Plan {
  padding: 140px 45px;
}
.plan-content {
  text-align: center;
  justify-items: center;
}
.plan-content h1 {
  position: relative;
  font-size: 64px;
  width: fit-content;
  margin-bottom: 24px;
  margin-top: 140px;
}
.plan-element {
  z-index: -1;
  bottom: 0;
  right: -8px;
  width: 283px;
  position: absolute;
}
.plan-content p {
  font-size: 18px;
  width: 78%;
}

.swiper {
  margin: 60px 45px 0 45px !important;
}
.swiper-wrapper {
  display: flex;
  align-items: center !important;
}
/* --- SWIPER SLIDE LAYOUT --- */
.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* --- BASE CARD (INACTIVE STATE) --- */
.plan-card {
  background: #ffffff;
  border: 2px solid #ffe492;
  border-radius: 14px;
  padding: 40px 44px; /* default inactive padding */
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.06);
  transition: all 0.3s ease;
  transform-origin: center center;
}

.plan-card h5 {
  font-size: 24px;
  margin-bottom: 25px;
}
.plan-card h2 {
  margin: 0 0 25px 0;
  font-size: 36px;
  font-weight: 700;
}

.plan-card p {
  margin-bottom: 25px;
  font-weight: 500;
}

.feature-list li {
  display: flex;
  gap: 10px;
  left: 25px;
  margin-bottom: 28px;
  font-size: 16px;
  align-items: center;
  position: relative;
}
.feature-list svg {
  position: absolute;
  width: 18px !important;
  height: 18px !important;
  left: -25px;
}

/* --- BUTTON (INACTIVE STATE) --- */
.plan-card button {
  width: 60%;
  padding: 12px 14px;
  border-radius: 8px;
  border: 2px solid #ffe492;
  background: #ffffff;
  font-size: 16px;
  font-weight: 600;
  transition: all 0.3s ease;
}

/* ------------------------------------------
   ACTIVE + HOVERED SLIDE
------------------------------------------- */
.swiper-slide:hover .plan-card {
  background: #043873 !important;
  padding: 80px 44px !important; /* ACTIVE PADDING */
  border-color: #043873 !important;
  color: #ffffff !important;
  box-shadow: 0 18px 40px rgba(4, 56, 115, 0.25);
}

/* ACTIVE TEXT COLOR */
.swiper-slide:hover .plan-card h2 {
  color: #ffe492 !important;
}
.swiper-slide:hover .plan-card h5,
.swiper-slide:hover .plan-card p {
  color: #ffffff !important;
}

/* ACTIVE ICONS */
.swiper-slide:hover .plan-card svg {
  stroke: #ffe492 !important;
}

/* ACTIVE BUTTON */
.swiper-slide:hover .plan-card button {
  background: #4f9cf9 !important;
  border-color: #4f9cf9 !important;
  color: #ffffff !important;
  cursor: pointer;
}
