@import "../../var.css";

#Home {
  width: 100vw;
  background-color: var(--primary-color);
  background-image: url(./Element.png);
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  padding: 110px 45px;
}
.hero-text-container {
  width: 60%;
}
.hero-text-container h1,
.hero-text-container p {
  color: var(--text-color);
  margin-bottom: 60px;
}
.hero-text-container h1 {
  font-size: 64px;
  margin-bottom: 24px;
}
.hero-text-container p {
  font-size: 18px;
}
.buttons {
  padding: 20px;
  background-color: var(--btn2-color);
  font-size: 18px;
  color: var(--text-color);
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: 300ms ease;
}
.buttons:hover {
  background-color: hsl(from var(--btn2-color) h s 56%);
}
#arrowright {
  font-size: 18px;
  margin-bottom: -3px;
}

.image-container {
  width: 50%;
  aspect-ratio: 1/0.7;
  background-color: hsl(from var(--btn2-color) h s 80%);
  background-image: url(../../global/heroimage.jpg);
  background-size: cover;
  border-radius: 5px;
}

@media (max-width: 1152px) {
  #Home {
    padding: 140px 45px;
  }
  .hero-text-container p {
    width: 80%;
  }
  .image-container {
    height: 400px;
  }
}
@media (max-width: 1038px) {
  .image-container {
    width: 60%;
    height: calc(100vh - 140px);
  }
}
@media (max-width: 1014px) {
  .hero-text-container h1 {
    line-height: 1.1;
  }
  .image-container {
    width: 60%;
    height: calc(100vh - 100px);
  }
  .hero-text-container p {
    margin-bottom: 45px;
  }
}
@media (max-width: 930px) {
  .hero-text-container h1 {
    font-size: 54px;
  }
}
@media (max-width: 768px) {
  #Home {
    flex-direction: column;
    padding: 140px 20px;
    text-align: center;
  }
  .hero-text-container {
    width: 100%;
  }
  .hero-text-container h1 {
    line-height: normal;
  }
  .hero-text-container p {
    width: 100%;
    margin-bottom: 60px;
  }
  .hero-btn {
    margin-bottom: 100px;
  }
  .image-container {
    width: 90%;
    aspect-ratio: 1/0.6;
    height: auto;
  }
}
@media (max-width: 430px) {
  #Home {
    padding: 80px 16px;
  }
  .hero-text-container h1 {
    font-size: 36px;
  }
  .image-container {
    width: 95%;
    aspect-ratio: 1/0.7;
  }
}
