@import "../../var.css";
.login-container {
  width: 100%;
  height: 100vh;
  align-content: center;
  justify-items: center;
  background-image: radial-gradient(
    circle,
    hsl(from var(--btn2-color) h s 80%) 0%,
    hsl(from var(--btn2-color) h s 35%) 60%
  );
  position: relative;
}
.bg-logo {
  position: absolute;
  width: 800px;
  right: 18vw;
  top: 1vw;
}
.login,
.signup {
  position: relative;
  z-index: 2;
  border: 2px solid var(--btn1-color);
  border-radius: 5px;
  padding: 20px 30px;
  margin-top: 80px;
  background-color: rgb(from var(--primary-color) r g b / 1);
}
.login h2,
.signup h2 {
  text-align: center;
  font-size: 32px;
  margin-bottom: 20px;
  color: var(--text-color);
}
.labels {
  font-size: 18px;
  color: var(--text-color);
  font-weight: 500;
}
.inputs {
  padding: 3px;
  font-size: 16px;
  margin-bottom: 5px;
  width: 100%;
}
.inputs:focus {
  outline: none;
}
.submit-btn {
  width: 100%;
  padding: 8px 0;
  border-radius: 20px;
  margin: 10px 0 20px 0;
  cursor: pointer;
  background-color: var(--btn1-color);
  font-size: 16px;
  font-weight: 600;
  color: var(--primary-color);
}
.submit-btn:hover {
  background-color: hsl(from var(--btn1-color) h s 70%);
}
#p {
  font-size: 16px;
  color: var(--text-color);
  text-align: center;
}
#p a {
  color: var(--btn1-color);
}
