@import "../../var.css";
#Extension {
  padding: 140px 45px;
  display: flex;
  background-color: var(--primary-color);
  overflow: hidden;
}
.overflow-hidden {
  overflow: hidden;
}
.extension-content {
  width: 592px;
  color: white;
  align-content: center;
}
.extension-content h1 {
  font-size: 72px;
  margin-bottom: 24px;
  position: relative;
  z-index: 2;
}
.extension-content p {
  font-size: 18px;
  margin-bottom: 60px;
}
.ext-element {
  z-index: -1;
  position: absolute;
  width: 350px;
  bottom: 0;
  right: -30px;
}
.extension-frame {
  width: 540px;
  height: 470px;
  margin-left: 98px;
  background-color: hsl(from var(--btn2-color) h s 80%);
  background-image: url("../../global/extension.png");
  background-size: 1055px;
  background-position-x: -105px;
  background-position-y: -135px;
  background-repeat: no-repeat;
}
