@import "../../var.css";
#Sponser {
  padding: 140px 45px;
  text-align: center;
}
#Sponser h1 {
  font-size: 64px;
  position: relative;
  margin-bottom: 100px;
}
.sponser-element {
  position: absolute;
  z-index: -1;
  bottom: 0;
  right: 310px;
  width: 350px;
}
.sponser-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 71px;
}
.sponser-container div {
  display: inherit;
  align-items: center;
}
.sponser-container div h2 {
  font-size: 45px;
}
.sponser-container img {
  width: 60px;
}
.google {
  width: 200px !important;
}
