@import "../../var.css";
.dashboard {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 20% 1fr;

  grid-template-rows: 60px 1fr;
}
.row {
  grid-column: 1 / 3;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 45px;
  width: 100%;
  height: 60px;
  background-color: var(--primary-color);
}
.row img {
  height: 35px;
}
.row h2 {
  color: white;
  font-size: 34px;
  font-weight: 600;
}
.logout-btn {
  padding: 10px 15px;
  color: var(--primary-color);
  background-color: var(--btn1-color);
  cursor: pointer;
  font-size: 16px;
  border-radius: 10px;
  border: none;
  font-weight: 600;
}
.logout-btn:hover {
  background-color: hsl(from var(--btn1-color) h s 70%);
}
.sidebar {
  width: 260px;
  grid-column: 1 / 2;
  background-color: hsl(from var(--primary-color) h s 70%);
  grid-row: 2 / 3;
}
.dashcontent {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  background-color: hsl(from var(--primary-color) h s 90%);
  padding: 20px;
}
